import React from 'react';
import { useEffect,useState } from 'react';

import { useNavigate } from 'react-router-dom';


function MyComponent() {
  //const [inviteId, setinviteId] = useState(null);
  //const shouldBlockNavigation = true;
  const [shouldBlockNavigation, setShouldBlockNavigation] = useState(true); // determina se bloccare la navigazione o meno
  const navigate = useNavigate();
  const inviteId = localStorage.getItem('invtoId');
  useEffect(() => {
    
    const handleBeforeUnload = (e) => {
      fetch(`http://localhost:8080/invitations/${inviteId}/offbyid`, {
        method: 'PUT',
      })
        .then(response => {
          console.log('API call successful');
        })
        .catch(error => {
          console.log('API call failed', error);
        });
    };

    window.addEventListener('beforeunload', handleBeforeUnload);

    return () => {
      
      window.removeEventListener('beforeunload', handleBeforeUnload);
    };
  }, [inviteId]);


 
  const handleConfirmNavigation = () => {
    //const onpage = 'pagina_successiva'; // valore di esempio
    fetch(`http://localhost:8080/invitations/${inviteId}/onbyid`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
      },
     
    })
      .then(response => response.json())
      .then(data => {
        console.log(data.message);
         // naviga alla pagina specificata
      })
      .catch(error => {
        console.log(error);
      });
  
  };
/*
  const handleAbandonNavigation = () => {
    // esegui il codice per abbandonare l'azione
    // e chiamare l'API per impostare onpage a false
    fetch(`http://localhost:8080/invitations/${senderId}/off`, {
      method: 'PUT',
    })
      .then(response => response.json())
      .then(data => {
        console.log(data.message);
        // naviga alla home page
      })
      .catch(error => {
        console.log(error);
      });
  };

  useEffect(() => {
    window.onbeforeunload = () => {
      handleAbandonNavigation();
    };
  }, []);

*/
  return (
    <div>
      {}
      {shouldBlockNavigation && (
        <button onClick={handleConfirmNavigation}>Conferma</button>
      )}
    </div>
  );
}

export default MyComponent;
